.sidebar{
    height: 100vh !important;
    background-color: black;
    box-shadow: 0 0 2px grey;
    color:antiquewhite;
}

.menu{
    padding-top: 100px;
}

.menu-item{
    padding-top: 30px;
}

.menu-item a{
    color:white;
    text-decoration: none;
    font-size: 1.2rem;
}

.menu-item i{
    font-size: 1.2rem;
    margin: 0 15px;
    color:white;
}

.active{
    background-color: white;
    padding: 10px;
    margin-top: 25px;
}

.active a,.active i{
    color:black
}